.header{
    background-color: $fondoHero-footer;
    padding-bottom: calc($separacion * 2.5);
}
.hero{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    .hero-info{
        display: flex;
        flex-direction: column;
    }
    .hero-title{
        font-size: 5.5rem;
        line-height: 6.5rem;
    }
    .hero-img{
        .hero__img{
            width: 100%;
            height: 100%;
        }
    }
}
.rate{
    display: flex;
    align-items: center;
    column-gap: calc($separacion / 4);
    &:last-of-type{
        color: $negro;
    }
    .stars{
        display: flex;
        column-gap: 0.5rem;
        align-items: center;
        .star{
            @include img('../icons/star.svg');
            width: 1.5rem;
            height: 1.5rem;
        }
    }
}
.trial{
    display: flex;
    align-items: center;
    column-gap: calc($separacion / 1.5);
    margin: calc($separacion / 2.5) 0;
    .trial-btn{
        @include btn;
    }
    .examples-btn{
        color: $colorPrincipal;
        font-weight: $bold;
        border-bottom: 3px solid $colorPrincipal;
    }
}
.benefits{
    display: flex;
    align-items: center;
    column-gap: calc($separacion / 2);
    .benefit{
        display: flex;
        align-items: center;
        column-gap: 1rem;
        .check{
            @include img('../icons/Checkmark.svg');
            height: 2.3rem;
            width: 2.3rem;
        }
    }
}