.main{
    padding-top: calc($separacion * 2);
}
.reasons{
    .reasons-question{
        color: $colorPrincipal;
        font-weight: $bold;
        font-size: 2rem;
        margin-bottom: 1.5rem;
    }
    .reasons-title{
        width: 50%;
        font-size: 3.5rem;
        line-height: 4.5rem;
    }
}
.reasons-grid{
    margin-top: $separacion;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: calc($separacion / 1.5);
    .reason{
        .reason__logo{
            width: 4rem;
            height: 4rem;
        }
        .reason__title{
            font-size: 1.6rem;
            font-weight: $bold;
            margin: 1.5rem 0;
        }
    }
}
.g1{
    @include img('../icons/time.svg')
}
.g2{
    @include img('../icons/code.svg')
}
.g3{
    @include img('../icons/allSizes.svg')
}