.footer{
    background-color: $fondoHero-footer;
    margin-top: calc($separacion * 1.5);
    padding: $separacion 0;
    .footer-container{
        display: grid;
        grid-template-columns: 45% 55%;
        .footer-info-container{
            display: flex;
            flex-direction: column;
            row-gap: calc($separacion / 4);
            padding-right: $separacion;
        }
        .footer-links{
            display: flex;
            justify-content: space-between;
            column-gap: calc($separacion / 2);
            .links{
                display: flex;
                flex-direction: column;
                row-gap: calc($separacion / 4);
                .link{
                    font-size: 1.6rem;
                }
            }
        }
    }
}
