.nav-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: calc($separacion / 1.5) 0;
    .logo{
        font-weight: $bold;
        font-size: 2rem;
        color: $negro;
    }
    .nav-btns{
        display: flex;
        align-items: center;
        column-gap: calc($separacion / 2);
        .nav__btn{
            color: $negro;
        }
    }
    .login-btns{
        display: flex;
        align-items: center;
        column-gap: calc($separacion / 2);
        &:first-of-type{
            font-weight: $bold
        }
        .logo__btn{
            color: $negro;
        }
        .btn{
            @include btn;
        }
    }
}
