.reviews-container{
    overflow-x: auto;
    white-space: nowrap;
    margin-bottom: 5rem;
        .review-card{
            width: 45rem;
            padding: calc($separacion / 2);
            border-radius: 0.5rem;
            border: 2px solid #a8a8a856;
            display: inline-block; 
            white-space: normal;
            margin-left: calc($separacion / 2);
            &:first-of-type{
                margin: 0;
            }
            .review-info{
                margin: calc($separacion / 4) 0;
                font-weight: $medium
            }
            .btn-review{
                @include btnWhite;
                display: inline-block;
                width: 100%;
                text-align: center;
                border: 2px solid #a8a8a856;
            }
        }
}
.review-person{
    .person{
        display: flex;
        align-items: center;
        column-gap: calc($separacion / 4);
        .review-avatar{
            border-radius: 50%;
        }
        .info-person{

            .review-name{
                color: $colorPrincipal;
                font-weight: $bold;
                font-size: 1.6rem;
            }
            .review-money{
                font-size: 1.6rem;
                font-weight: $medium;
            }
        }
    }


}